export default {
	data() {
		return {
			enImg:''
		};
	},
	methods: {
		//跳转页面
		opennew: function(id) {
			uni.navigateTo({
				url: '../' + id + '/' + id
			});
		},
		upimg: function() {
			var that=this
			//调取相册
			uni.chooseImage({
				count: 1, //默认9
				success: function(res) {
					console.log(res.tempFilePaths[0]);
					uni.uploadFile({
						url: getApp().globalData.byurl + '/buyer/base/upload', //仅为示例，非真实的接口地址
						filePath: res.tempFilePaths[0],
						name: 'file',
						formData: {
							scene: 'idcard'
						},
						success: uploadFileRes => {
							that.enImg=JSON.parse(uploadFileRes.data).url
						},
						fail: err => {
							console.log(err);
						}
					});
				},
				fail: function(fa) {
					//提示框
					uni.showToast({
						title: '失败，请重试',
						duration: 2000
					});
				}
			});
		}
	}
};
